.source-panel__card {
    .card-header {
        [data-toggle="collapse"] {
            &:after {
                content: "\25BC";
            }

            &[aria-expanded="true"] {
                &:after {
                    content: "\25B2";
                }
            }
        }
    }
}

.source-panel__container {
    background-color: white;
    color: black;
    position: relative;

    pre {
        color: black;
    }

    .copy-to-clipboard {
        position: absolute;
        right: 0.5rem;
        top: 0.5rem;
    }
}

.source-panel__card {
    .card-header {
        .btn {
            padding: 0.375rem 0.75rem;
        }
    }
}
